<template>
    <div class="home" ref="homeRef">
        <loading v-if="loading"></loading>
        <card v-for="blog in blogs"
              :key="blog.id"
              :avatar="blog.owner.avatar_url"
              :author="blog.owner.login"
              :time="blog.created_at"
              :des="blog.description"
              :comments="blog.comments"
              @click.native="clickCard(blog)"
        ></card>
        <edit></edit>
    </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  import Card from '../components/Card'
  import Loading from '../components/Loading'
  import Edit from '../views/Edit'

  export default {
    name: 'home',
    components: {
      'card': Card,
      'loading': Loading,
      'edit': Edit
    },
    data() {
      return {
        loading: false,
        blogs: []
      }
    },
    methods: {
      clickCard(blog) {
        this.$router.push({
          name: 'detail',
          query: {
            id: blog.id
          }
        })
      },
      getBlogs(username) {
        if(username) {
          this.loading = true;
          this.$ajax.get(`https://api.github.com/users/${username}/gists`).then((res) => {
            this.blogs = this.blogs.concat(res.data);
            this.loading = false;
          })
        }else {
          this.blogs = []
        }
        // this.$ajax.post('https://api.github.com/gists?access_token=1ee4ac79aceb9c12a908d1a59a7c9d842ed6eba0',{
        //   "description": "Hello World Examples",
        //   "public": true,
        //   "files": {
        //     "hello_world.rb": {
        //       "content": "class HelloWorld\n   def initialize(name)\n      @name = name.capitalize\n   end\n   def sayHi\n      puts \"Hello !\"\n   end\nend\n\nhello = HelloWorld.new(\"World\")\nhello.sayHi"
        //     },
        //     "hello_world.py": {
        //       "content": "class HelloWorld:\n\n    def __init__(self, name):\n        self.name = name.capitalize()\n       \n    def sayHi(self):\n        print \"Hello \" + self.name + \"!\"\n\nhello = HelloWorld(\"world\")\nhello.sayHi()"
        //     },
        //     "hello_world_ruby.txt": {
        //       "content": "Run `ruby hello_world.rb` to print Hello World"
        //     },
        //     "hello_world_python.txt": {
        //       "content": "Run `python hello_world.py` to print Hello World"
        //     }
        //   }
        // }).then((res)=>{
        //
        // })
      }
    },
    computed: {
      // 使用对象展开运算符将 getter 混入 computed 对象中
      ...mapGetters([
        'GET_USER'
      ])
    },
    watch: {
      GET_USER(user) {
        this.getBlogs(user.username)
      }
    },
    //记住滚动位置
    beforeRouteLeave(to, from, next) {
      this.$store.commit('SET_SCROLLTOPS', {
        key: 'home',
        value: this.$refs.homeRef.scrollTop
      });
      next();
    },
    beforeRouteEnter(to, from, next) {
      next(vm => {
        vm.$refs.homeRef.scrollTop = vm.$store.state.scrollTops.home
      })
    }
  }
</script>

<style lang="scss" scoped>
    .home {
        position: absolute;
        top: 46px;
        left: 0;
        right: 0;
        bottom: 0;

        overflow: auto;
        -webkit-overflow-scrolling: touch;

    }
</style>
