<template>
    <div id="app">
        <nav class="nav">
            <router-link class="nav-left" to="/">
                <svg class="nav-left-icon">
                    <use xlink:href="#icon-logo"></use>
                </svg>
                <span class="nav-left-title">
                    欢迎考砸的孩子
                </span>
            </router-link>
            <div class="nav-right">
                <svg class="nav-right-icon" @click="clickLogin" v-if="JSON.stringify(GET_USER)==='{}'">
                    <use xlink:href="#icon-login"></use>
                </svg>
                <div class="nav-right-avatar" v-else @click="clickLogout">
                    <img :src="GET_USER.avatar" alt="" width="100%" height="100%">
                </div>
            </div>
        </nav>
        <div class="section">
            <keep-alive>
                <router-view/>
            </keep-alive>
        </div>
        <my-dialog v-model="show" :before-close="beforeClose" :confirm-text="confirmText">
            <my-input class="login-input" v-model="username" :label="usernameText" :placeholder="usernamePlaceholder" ref="usernameInputRef"></my-input>
            <my-input class="login-input" v-model="token" :label="passwordText" :placeholder="passwordPlaceholder" ref="passwordInputRef"></my-input>
        </my-dialog>
    </div>
</template>

<script>
  import Dialog from './components/Dialog'
  import Input from './components/Input'
  import { mapGetters } from 'vuex'

  export default {
    components: {
      'my-dialog': Dialog,
      'my-input': Input
    },
    data() {
      return {
        show: false,
        confirmText: '绑定',
        usernameText: '用户名',
        usernamePlaceholder: '请输入GitHub用户名',
        passwordText: 'Token',
        passwordPlaceholder: '请输入有效Token',
        username: 'Lkkkkkkg',
        token: '19cc48c48e1f536996072dd81994a7cd8998449b'
      }
    },
      computed: {
          // 使用对象展开运算符将 getter 混入 computed 对象中
          ...mapGetters([
              'GET_USER'
          ])
      },
    methods: {
      clickLogin() {
        this.show = true
      },
      clickLogout() {
        this.$store.commit('SET_USER', {})
      },
      beforeClose(action,done) {
        if(action === 'on-confirm') {
          this.$ajax(`https://api.github.com/user?access_token=${this.token}`).then((res)=>{
            if(res.status === 200) {
              if(this.username === res.data.login) {
                console.log('登陆成功');
                this.$store.commit('SET_USER', {
                  username: res.data.login,
                  avatar: res.data.avatar_url
                })
                this.$refs.usernameInputRef.doExit()
                this.$refs.passwordInputRef.doExit()
                done()
              }else {
                  console.log('账号与Token不一致');
                  done(false)
              }
            }
          }).catch(()=>{
            console.log('账号与Token不一致');
            done(false)
          })
        }else {
          this.$refs.usernameInputRef.doExit()
          this.$refs.passwordInputRef.doExit()
          done()
        }
      }
    }
  }
</script>

